@font-face {
  font-family: "Open Sans";
  font-weight: 400;
  font-style: normal;
  src: local("Open Sans Regular"), url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  src: local("Open Sans Light"), url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff");
}
@font-face {
  font-family: "Open Sans";
  font-weight: 700;
  font-style: normal;
  src: local("Open Sans Bold"), url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: auto;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: #444444;
}
a {
  text-decoration: none;
  color: #111111;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.title-text {
  font-family: "Open Sans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 18px;
}
.container {
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 1200px) {
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 130px;
  }
}
.container-inner {
  margin: 0 20px;
}
@media (min-width: 660px) {
  .container-inner {
    margin: 0 auto;
  }
}
.main-header {
  background-color: #283645;
}
.main-header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.main-header__toggle {
  width: 70px;
  height: 60px;
  background-image: url("../img/icon-menu-burger.svg");
  padding: 0;
  border: none;
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: right;
}
.main-header__toggle--open {
  width: 70px;
  height: 60px;
  background-image: url("../img/icon-menu-cross.svg");
  background-repeat: no-repeat;
  background-position: right;
}
@media (min-width: 660px) {
  .main-header__logo-image {
    width: 147px;
    height: 40px;
  }
}
@media (min-width: 1200px) {
  .main-header {
    margin-bottom: -110px;
    background-color: rgba(0, 0, 0, 0.3);
    position: relative;
    z-index: 10;
    padding: 35px;
  }
  .main-header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .main-header__logo {
    z-index: 10;
  }
  .main-header__toggle {
    display: none;
  }
  .main-header__wrap {
    padding: 0;
    opacity: 1;
  }
}
.header-nav__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.header-nav {
  display: none;
}
.header-nav--open {
  border-top: 1px solid #000000;
  margin: 0 -20px;
  display: block;
}
.header-nav__item {
  text-align: center;
  padding: 20px 0;
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
}
.header-nav__link {
  font-size: 18px;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
}
.header-nav__link:hover {
  color: #d22856;
}
.header-nav__link:focus {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 1200px) {
  .header-nav__list {
    display: flex;
  }
  .header-nav__link {
    font-weight: 400;
    text-transform: none;
  }
  .header-nav__item {
    border: none;
    margin-left: 80px;
    padding: 0;
  }
  .header-nav__item--active {
    border-bottom: 2px solid #ffffff;
    padding-bottom: 9px;
  }
  .header-nav__item--active:hover {
    border-bottom: 2px solid #d22856;
  }
  .header-nav__item--active:focus {
    border-bottom: 2px solid rgba(255, 255, 255, 0.3);
  }
  .header-nav {
    display: flex;
    border: none;
    z-index: 10;
    margin: 0;
  }
}
.promo {
  background-image: url("../img/bg-back-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: 30px;
}
.promo__title {
  margin: 0;
  font-size: 24px;
  font-weight: 300;
  color: #ffffff;
  text-align: center;
}
.promo__image {
  margin: 20px auto 0;
  display: block;
}
@media (min-width: 660px) {
  .promo {
    background-image: url("../img/bg-back-tablet.jpg");
    position: relative;
    padding-top: 75px;
    padding-bottom: 112px;
  }
  .promo__container {
    display: flex;
    flex-direction: column;
    margin: 0;
    width: 300px;
  }
  .promo__title {
    font-size: 40px;
    max-width: 200px;
    align-self: center;
    display: flex;
  }
  .promo__image {
    position: absolute;
    right: 0;
    bottom: 0;
  }
}
@media (min-width: 1200px) {
  .promo {
    padding-top: 230px;
    padding-bottom: 125px;
  }
  .promo__image {
    right: auto;
  }
}
.platforms {
  background-color: #1d2631;
  text-align: center;
  padding-top: 30px;
  padding-bottom: 35px;
  margin: 0 -20px;
}
.platforms__button {
  color: #ffffff;
  border: 3px solid #ffffff;
  border-radius: 30px;
  padding: 18px 0;
  font-size: 18px;
  font-weight: 700;
  line-height: 1;
  text-align: center;
  display: block;
  margin-top: 40px;
}
.platforms__button:hover {
  box-shadow: 0 5px 7px 0 rgba(1, 1, 1, 0.75);
  border: solid 3px #ffffff;
  background-color: #ffffff;
  color: #d22856;
}
.platforms__button:focus {
  color: rgba(210, 40, 86, 0.3);
}
.platforms__container {
  margin-top: 30px;
}
.platforms__text {
  font-weight: 300;
  color: #ffffff;
}
.platforms__item {
  margin-left: 35px;
}
.platforms__item:first-child {
  margin-left: 0;
}
@media (min-width: 660px) {
  .platforms {
    background-color: transparent;
    max-width: 350px;
    padding: 0;
    margin: 0;
    z-index: 10;
    position: relative;
  }
  .platforms__text {
    font-size: 18px;
  }
}
.advantages__list-inner {
  position: relative;
  padding-bottom: 300px;
}
.advantages__list-inner::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 157px;
  height: 320px;
  background-image: url("../img/iphone-screen-mobile@1x.png");
  background-position: center 43px;
  background-repeat: no-repeat;
}
.advantages__text {
  font-size: 24px;
  font-weight: 300;
  text-align: center;
  margin: 30px 0 0;
}
.advantages__text-item {
  color: #283645;
  display: block;
  padding-top: 30px;
}
.advantages__list-container {
  background-color: #f2f2f2;
  z-index: 0;
  padding-top: 130px;
  position: relative;
}
.advantages__list-container::before {
  content: "";
  z-index: 1;
  position: absolute;
  background-image: url("../img/bg-triangle-white.svg");
  background-repeat: no-repeat;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 170px;
  background-color: transparent;
}
.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  text-align: center;
}
@media (min-width: 660px) {
  .advantages__list {
    max-width: 600px;
    text-align: left;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 20px;
    padding-left: 308px;
  }
  .advantages__list-inner {
    padding-bottom: 0;
    z-index: 10;
  }
  .advantages__list-inner::after {
    content: "";
    width: 268px;
    height: 547px;
    background-image: url("../img/iphone-screen-3-tablet@1x.png");
    background-position: left 65px;
    background-repeat: no-repeat;
    margin-left: 100px;
  }
  .advantages__item {
    margin-bottom: 25px;
    margin-top: 25px;
  }
  .advantages__list-container {
    padding-bottom: 0;
    padding-top: 80px;
  }
  .advantages__text {
    font-size: 40px;
    margin-top: 60px;
  }
  .advantages__text-item {
    padding: 0;
  }
}
@media (min-width: 1200px) {
  .advantages__list-inner::after {
    display: none;
  }
  .advantages__text {
    font-size: 50px;
    margin-bottom: 75px;
  }
  .advantages__list {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 0;
    max-width: 100%;
    padding-left: 0;
  }
  .advantages__item {
    font-size: 18px;
    width: 240px;
    text-align: center;
    margin: -45px 0 0 90px;
  }
  .advantages__item:first-child {
    margin-left: 0;
  }
  .advantages__item--happy::before {
    content: "";
    display: block;
    background-image: url("../img/iphone-screen-1-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: center;
    height: 382px;
  }
  .advantages__item--effect::before {
    content: "";
    display: block;
    background-image: url("../img/iphone-screen-2-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: center;
    height: 382px;
  }
  .advantages__item--friends::before {
    content: "";
    display: block;
    background-image: url("../img/iphone-screen-3-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: center;
    height: 382px;
  }
  .advantages__title {
    font-size: 30px;
    font-weight: 700;
    line-height: 30px;
    margin: 0 0 30px;
  }
  .advantages__item-text {
    margin: 0;
  }
  .advantages__list-container {
    padding: 0;
  }
  .advantages__list-container::before {
    background-size: cover;
  }
}
.effect {
  display: none;
}
@media (min-width: 660px) {
  .effect {
    display: block;
    margin-top: 60px;
  }
  .effect__container {
    display: flex;
    align-items: center;
    position: relative;
  }
  .effect__container::before {
    content: "";
    height: 276px;
    display: block;
    background-image: url("../img/icon-timer.svg");
    background-repeat: no-repeat;
    background-size: 238px auto;
    width: 238px;
    margin-left: 80px;
  }
  .effect__container::after {
    content: "24";
    position: absolute;
    width: 202px;
    height: 202px;
    display: block;
    border-radius: 50%;
    border: 15px solid #d22856;
    color: #d22856;
    font-size: 96px;
    font-weight: 700;
    text-align: center;
    top: 55px;
    left: 115px;
  }
  .effect__inner {
    width: 50%;
    max-width: 400px;
    margin-left: auto;
    margin-right: auto;
    padding-bottom: 20px;
  }
  .effect__title {
    font-size: 30px;
    font-weight: 700;
    color: #000000;
  }
  .effect__link {
    color: #d22856;
    border-bottom: 1px solid #d22856;
  }
}
@media (min-width: 1200px) {
  .effect {
    background-color: #f2f2f2;
    margin-top: 0;
    padding-top: 60px;
    padding-bottom: 120px;
  }
  .effect__inner {
    max-width: 550px;
  }
  .effect__text,
  .effect__link {
    font-size: 18px;
  }
  .effect__container::after {
    left: 150px;
  }
}
.review {
  text-align: center;
  margin-top: 40px;
}
.review__comment {
  line-height: 2.14;
}
.review__age {
  display: none;
}
.review__name {
  margin-top: 0;
}
@media (min-width: 660px) {
  .review {
    margin-top: 100px;
  }
  .review__age {
    display: block;
    color: #283645;
    font-size: 14px;
    font-weight: 400;
  }
  .review__slide {
    display: flex;
    flex-direction: column-reverse;
  }
  .review__comment {
    font-size: 18px;
    margin: 0;
  }
}
.slider__control {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 45px;
}
.slider__control-item {
  display: block;
  width: 6px;
  height: 6px;
  opacity: 0.3;
  background-color: #283645;
  border-radius: 50%;
  margin-left: 19px;
}
.slider__control-item:first-child {
  margin-left: 0;
}
.slider__control-item--active {
  width: 12px;
  height: 12px;
  opacity: 1;
  position: relative;
}
.slider__control-item--active::after {
  content: "";
  position: absolute;
  top: 3px;
  left: 3px;
  display: block;
  width: 6px;
  height: 6px;
  background-color: #ffffff;
  border-radius: 50%;
}
@media (min-width: 660px) {
  .slider__control--tablet {
    display: none;
  }
}
.rates {
  background-color: #f2f2f2;
  overflow: hidden;
  z-index: 0;
  padding-top: 150px;
  padding-bottom: 50px;
  position: relative;
}
.rates::before {
  content: "";
  z-index: 1;
  position: absolute;
  background-image: url("../img/bg-triangle-white.svg");
  background-repeat: no-repeat;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 170px;
  background-color: transparent;
}
.rates__name {
  background-color: #283645;
  color: #ffffff;
  text-transform: uppercase;
}
.rates__name-item {
  font-size: 18px;
  text-align: center;
  background: #283645;
  padding-top: 20px;
  padding-bottom: 15px;
  font-weight: 600;
  line-height: 1.6;
}
.rates__table {
  table-layout: fixed;
  position: relative;
  width: 834px;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 45px;
}
.rates__price {
  font-size: 24px;
  font-weight: 300;
  display: block;
}
.rates__feature-item {
  background-color: #ffffff;
  padding: 20px;
  width: 276px;
  position: relative;
  text-transform: uppercase;
}
.rates__feature-item--hidden {
  display: none;
}
.rates__feature__mobile-icon {
  position: absolute;
  right: 20px;
  top: 20px;
}
.rates__name-item--best {
  position: relative;
}
.rates__name-item--best::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  background-image: url("../img/bg-hit.svg");
  background-repeat: no-repeat;
  width: 74px;
  height: 73px;
}
.rates__name-item--best::after {
  content: "хит";
  font-size: 14px;
  font-weight: 500;
  transform: rotate(-45deg);
  position: absolute;
  text-transform: uppercase;
  left: 15px;
  top: 15px;
}
.rates__name-item--empty {
  display: none;
}
@media (min-width: 660px) {
  .rates__feature-item--hidden {
    display: table-cell;
  }
  .rates__feature-item--mobile {
    display: none;
  }
  .rates__name-item {
    font-size: 16px;
  }
  .rates__table {
    width: 100%;
  }
  .rates__feature-item {
    font-size: 14px;
    text-transform: none;
    padding: 15px 15px;
    text-align: center;
  }
  .rates__feature-item--option {
    text-align: left;
  }
  .rates__price {
    font-size: 18px;
  }
  .rates__name-item--empty {
    display: table-cell;
    background-color: #f2f2f2;
  }
  .rates__name-item--best::before {
    content: "";
    background-image: url("../img/bg-hit-tablet.svg");
    width: 61px;
    height: 61px;
  }
  .rates__name-item--best::after {
    font-size: 10px;
    font-weight: 600;
    left: 10px;
    top: 10px;
  }
}
@media (min-width: 1200px) {
  .rates::before {
    background-size: cover;
  }
}
.contacts__text {
  font-size: 24px;
  font-weight: 300;
  text-align: center;
}
.contacts__text-item {
  display: block;
}
.contacts__list {
  text-align: center;
  margin: 0 -20px;
}
.contacts__title {
  margin-bottom: 0;
}
.contacts__link {
  font-size: 24px;
  font-weight: 300;
  color: #d22856;
  margin: 0;
}
.contacts__link:hover {
  color: #283645;
  border-bottom: 1px solid #283645;
}
.contacts__link:focus {
  color: rgba(40, 54, 69, 0.3);
  border: none;
}
.contscts__title {
  margin: 0;
}
@media (min-width: 660px) {
  .contacts__text-item {
    font-size: 40px;
  }
  .contacts__list {
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
  }
  .contacts__item {
    flex: 0 0 240px;
    margin: 0 20px;
  }
}
@media (min-width: 1200px) {
  .contacts__text-item {
    font-size: 50px;
  }
  .contacts__item-address {
    order: 2;
  }
  .contacts__item-mail {
    order: 3;
  }
}
.main-footer {
  display: block;
  background-color: #ffffff;
  margin-top: 40px;
  margin-bottom: 40px;
}
.main-footer--index {
  display: none;
}
.main-footer__container {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.main-footer__social {
  margin-top: 40px;
  margin-bottom: 40px;
  display: flex;
}
.main-footer__copyright-link {
  fill: #d22856;
  margin-left: 5px;
}
.main-footer__copyright-text {
  display: flex;
  align-items: center;
  margin: 0;
}
.main-footer__copyright-image:hover {
  fill: #283645;
}
.main-footer__copyright-image:focus {
  fill: rgba(40, 54, 69, 0.3);
}
@media (min-width: 660px) {
  .main-footer {
    display: block;
    background-color: #ffffff;
    margin-top: 40px;
  }
  .main-footer__container {
    margin-top: 30px;
    margin-bottom: 50px;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1200px) {
  .main-footer__copyright-text {
    font-size: 18px;
  }
}
.map {
  position: relative;
  margin: 0;
}
.map::before {
  content: "";
  background-image: url("../img/bg-triangle-white-footer.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;
  position: absolute;
  display: block;
  height: 60px;
  fill: #ffffff;
  width: 100%;
  top: 0;
  left: 0;
}
.map__iframe {
  width: 100%;
  height: 520px;
  border: none;
}
@media (min-width: 660px) {
  .map::before {
    background-size: cover;
    height: 100px;
  }
}
@media (min-width: 1200px) {
  .map::before {
    height: 140px;
  }
}
.social {
  margin-left: -20px;
  margin-right: -20px;
}
.social__item {
  fill: #d22856;
  margin-left: 20px;
  width: 46px;
  height: 46px;
  border: solid 3px rgba(40, 54, 69, 0.1);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.social__item:hover {
  border: solid 3px #283645;
}
.social__item:focus {
  border: solid 3px rgba(40, 54, 69, 0.3);
}
.promo-page {
  background-image: url("../img/bg-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 0 -54px;
  background-size: cover;
  padding: 35px 0 85px;
}
.promo-page__title {
  font-size: 24px;
  font-weight: 300;
  text-align: center;
  color: #ffffff;
  margin: 0;
}
@media (min-width: 660px) {
  .promo-page {
    background-image: url("../img/bg-back-tablet@1x.jpg");
    background-position: center center;
  }
  .promo-page__title {
    font-size: 50px;
  }
}
@media (min-width: 1200px) {
  .promo-page {
    padding-top: 180px;
    background-image: url("../img/bg-back-desktop@1x.jpg");
  }
}
.gallery {
  text-align: center;
  padding-top: 50px;
  margin-bottom: 50px;
}
.gallery__text {
  color: #283645;
  margin-top: 0;
  margin-bottom: 55px;
  line-height: 25px;
}
.gallery__text--bottom {
  margin-bottom: 0;
}
.gallery__panorama {
  margin: 0 -20px;
  position: relative;
}
.gallery__panorama::before {
  content: "ПАНОРАМА ДНЯ";
  position: absolute;
  width: 140px;
  height: 36px;
  background-color: #d22856;
  font-size: 12px;
  font-weight: 500;
  color: #ffffff;
  display: block;
  text-transform: uppercase;
  top: 0;
  right: 20px;
  padding: 10px;
}
.gallery__panorama-img {
  width: 100%;
  display: block;
}
.gallery__list-photo {
  width: 100%;
  display: block;
}
.gallery__list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 20px;
  margin-bottom: 50px;
