  margin: 0;
  width: 100%;
  color: #283645;
  outline: none;
}
.user-form__input:hover {
  border: solid 2px #283645;
}
.user-form__input:focus {
  border: solid 2px #d22856;
}
.user-form__part-achieve,
.user-form__part-app {
  background-color: #f2f2f2;
