  margin: 0;
}
.user-form__label-checkbox {
  display: block;
  position: relative;
  padding-left: 50px;
  font-size: 18px;
  color: #283645;
  padding-bottom: 6px;
  padding-top: 5px;
  margin-top: 40px;
}
.user-form__label-checkbox::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 35px;
  height: 35px;
  border: solid 2px #e5e5e5;
  background-color: #ffffff;
  display: inline-block;
}
.user-form__label-checkbox::after {
  content: "";
  position: absolute;
  left: 6px;
  top: 6px;
  background-repeat: no-repeat;
  background-position: center;
  width: 22px;
  height: 16px;
  display: inline-block;
}
.user-form__label-checkbox:hover.user-form__label-checkbox::before {
  border: solid 2px #283645;
}
.user-form__label-checkbox:active.user-form__label-checkbox::before {
  border: solid 2px #d22856;
}
.user-form__input-checkbox:checked + .user-form__label-checkbox::after {
  background-image: url("../img/icon-yes.svg");
}
.user-form__label-radio {
  position: relative;
  padding-left: 50px;
  font-size: 18px;
  color: #283645;
  display: block;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-top: 39px;
}
.user-form__label-radio::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 37px;
  height: 36px;
  border: solid 2px #e5e5e5;
  border-radius: 50%;
  background-color: #ffffff;
  display: inline-block;
}
.user-form__label-radio::after {
  content: "";
  position: absolute;
  left: 11px;
  top: 10px;
  background-repeat: no-repeat;
  background-position: center;
  width: 15px;
  height: 15px;
  background-color: transparent;
  display: inline-block;
  border-radius: 50%;
}
.user-form__label-radio:hover.user-form__label-radio::before {
  border: solid 2px #283645;
}
.user-form__label-radio:active.user-form__label-radio::before {
  border: solid 2px #d22856;
}
.user-form__input-radio:checked + .user-form__label-radio::after {
  background-color: #d22856;
}
